#ocf_logo_style {
    border: 0;
    right:0;
    position: absolute;
}

/* Smartphones (landscape) ----------- */
@media only screen
and (min-width : 321px)
and (max-width : 1000px) {
/* Styles */
    #ocf_logo_style{
        position: relative;
    }
}



